import React, {Component} from 'react';
import styles from './style.module.css';
import Col from "react-bootstrap/Col";
import cn from 'classnames';
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";
import Profile from "./contents/Profile";
import Wrapper from "../common/core/Wrapper";
import Button from "../common/core/Button";
import Image from "react-bootstrap/Image";

import logoImagePath from "./images/logo_sm.png";
import SignFooter from "../common/parts/SignFooter";
import Company from "./contents/Company";
import Business from "./contents/Business";

const WIDTH = 700;
const BAR_UNIT_WIDTH = 70;
const UNIT_ROUND = 30;

const STEPS = ['Profile', 'Company', 'Business'];

const PROFILE = 0;
const COMPANY = 1;
const BUSINESS = 2;
const COMPLETE = 3;

class SignUp extends Component {
  state = {
    step: PROFILE,
  };

  nextStep = () => {
    if (this.state.step >= COMPLETE) return;
    this.setState({step: this.state.step+1});
    //
    window.scrollTo({top: 0});
  };

  stepContent = (step) => {
    switch (step) {
      case (PROFILE): return <Profile/>;
      case (COMPANY): return <Company/>;
      case (BUSINESS): return <Business/>;
      case (COMPLETE): return <div>Congratulations!...</div>
    }
  };

  render() {
    return (
      <Wrapper className={styles.wrapper}>
        <Row className={styles.hat}><Image height={40} className={styles.logoImage} src={logoImagePath}/></Row>
        <Container className={styles.main} style={{width: WIDTH}}>
          <Row className={styles.header}><span>Registration form</span></Row>
          <ProgressBar step={this.state.step}/>
          {this.stepContent(this.state.step)}
          <Row className={styles.buttonContainer}>
            {this.state.step !== COMPLETE ? <Button
              className={styles.button}
              onClick={this.nextStep}
              title={this.state.step === BUSINESS ? 'register' : 'next step'}
            /> : null}
          </Row>
          <SignFooter className={styles.footer}/>
        </Container>
      </Wrapper>
    );
  }
}

const ProgressBar = ({step}) => (
    <Row className={styles.barContainer} style={{width: WIDTH+BAR_UNIT_WIDTH}}>
      <Row
        className={styles.barLine}
        style={{
          top: UNIT_ROUND/2,
          left: BAR_UNIT_WIDTH/2,
          width: WIDTH
        }}
      >{}</Row>
      {STEPS.map((text, i) => <Unit text={text} complete={i < step}/>)}
    </Row>
);

const Unit = ({text, complete}) => (
  <div className={styles.barUnitContainer} style={{width: BAR_UNIT_WIDTH}}>
    <div
      className={styles.barUnitRound}
      style={{width: UNIT_ROUND, height: UNIT_ROUND}}
    >
      {complete ? <Check/> : null}
    </div>
    <div className={styles.barUnitText}>{text}</div>
  </div>
);

const Check = () => (
  <div
    className={styles.check}
    style={{
      width: UNIT_ROUND / 2,
      height: UNIT_ROUND / 3,
      marginTop: - UNIT_ROUND / 5,
    }}
  />
);

export default SignUp;