import React from 'react';
import {BrowserRouter as HashRouter, Link, Redirect, Route, Switch} from 'react-router-dom';
import SignIn from "./SignIn";
import SignUp from "./SignUp";

function App() {
  return (
    <HashRouter basename={'/'}>
      <Redirect to={`/login`}/>
      <Route path={`/login`} component={SignIn}/>
      <Route path={`/register`} component={SignUp}/>
    </HashRouter>
  );
}

export default App;
