import React, {Component} from 'react';
import styles from './style.module.css';
import cn from 'classnames';

class Button extends Component {
  state = {
    pressing: false,
  };

  componentDidMount() {
    this.refs.button.addEventListener('mousedown', this.onMouseDown);
    document.addEventListener('mouseup', this.onMouseUp);
  }

  onMouseDown = () => this.setState({pressing: true});
  onMouseUp = () => this.setState({pressing: false});

  render() {
    return (
      <button
        className={cn(styles.button, this.state.pressing && styles.pressing, this.props.className)}
        onClick={this.props.onClick}
        ref={'button'}
      >
        {this.props.title.toUpperCase()}
      </button>
    );
  }
}

export default Button;